import React, { Component } from 'react';
import AddItem from '../addItem/AddItem';
import Header from '../header/Header';
import Lists from '../lists/Lists';


class App extends Component {
  state = {
    lists: [],
    title: 'Shoppy',
    subtitle: 'Shop without leaving anything',
    selectedOption: undefined
  }

  componentDidMount = () => {
    try{
      const json = localStorage.getItem('option')
      const option = JSON.parse(json);
      this.setState (() => ({ option }))
    } catch (e) {

    }
  }

  componentDidUpdate = (prevProps, prevState) => {
    if(prevState.lists.length !== this.state.lists.length) {
      const json = JSON.stringify(this.state.lists)
      localStorage.setItem('lists', json)
    }
  }

  handleRemoveAll = () => {
    this.setState(() => ({lists: []}))
  }

  handleRemove = (itemToRemove) => {
    this.setState((prevState) => ({
        lists: prevState.lists.filter((item) => {
          return itemToRemove !== item
        })
    }))
  }

  handleAddItem = (item) => {
    if(!item) {
      return 'Enter Value';
    } else if(this.state.lists.indexOf(item) > -1) {
      return 'This item already exists'
    }

    this.setState((prevState) => ({ 
      lists: prevState.lists.concat(item)})
      )}
        

  render() {
    return (
      <div className="App">
        <Header 
          title = {this.state.title}
          subtitle = {this.state.subtitle}
        />
        <Lists 
          handleRemoveAll= {this.handleRemoveAll} 
          lists = {this.state.lists}
          handleRemove = {this.handleRemove}
        />
        <AddItem handleAddItem={this.handleAddItem}/>
      </div>
    );
  }
}

export default App;