import React from 'react';

const List = (props) => (
            <div className='option'>
                <p className= 'option__text'>
                  {props.count}. {props.listText}
                </p>
                <button 
                  className= 'button button--link'
                  onClick={()=>{props.handleRemove(props.listText)}}>Remove</button>
            </div>
)


export default List;